	module.exports = {"data":{"markdownRemark":{"html":"<h3><strong>King's College London</strong> (2015-2018)</h3>\n<p>I studied at King's College London from 2015-2018 where I graduated\nwith a First Class with Honours in <strong>BSc Computer Science With Management</strong>. During each\nyear of study I have been awarded the <em>Computer Science with Management Prize</em>.\nThe prize is given by the Department of Informatics for achieving the highest grade from the course.</p>\n<p>My Final Year Project (<em>Dissertation</em>) was an automated SQL grading tool\nwith partial grading. I have built a Ruby library and a Ruby on Rails\nweb application which allow students to submit SQL solutions\nand receive instant feedback and hints in case they make errors.\nThe source code is available on Github for the <a href=\"http://github.com/vladstoick/fyp_sql_assess\">Ruby library</a>\nand the <a href=\"https://github.com/vladstoick/fyp\">web application</a>. The final report's\nTeX is also on <a href=\"https://github.com/vladstoick/fyp_reports\">GitHub</a>.</p>\n<h3><strong>National College of Computer Science Tudor Vianu, Bucharest</strong> (2007-2015)</h3>\n<p>I have studied Computer Science since 1st grade and participated\nat various competitive programming contests including National Olympiads.\nDuring high school, I have participated at 2 National Olympiads of Computer Science\nand received <strong>two Silver Medals</strong>.</p>"}},"pathContext":{}}

